.logo {
    display: flex;
    color: white;
    gap: .8rem;
    font-family: 'FiraCode-B';
    font-size: 1.6rem; }
.header {
    padding: 3.2rem 0;
    display: flex;
    justify-content: space-between;
    &-ul {
        list-style: none;
        display: flex;
        color: #ABB2BF;
        font-size: 1.6rem;
        font-family: "FiraCode-M";
        gap: 3.2rem;
        padding: 0;
        margin: 0;
        @media (max-width: 767px) {
            display: none; } }
    &-li {
        color: gray;
        a {
            text-decoration: none;
            color: inherit; }
        &::before {
            content: '#';
            display: inline-flex;
            color: #C778DD; }
        &:hover {
            color: white;
            transition: .3s all; } } }
.header-ul-mobile {
    display: none;
    list-style: none;
    z-index: 100;
    @media (max-width: 767px) {
        display: flex;
        position: fixed;
        right: 16px;
        top: 25px;
        flex-direction: column;
        align-items: flex-end;
        gap: 5px; } }

.mobile-li-line-1 {
    width: 24px;
    height: 2px;
    background-color: white;
    transition: .5s all; }
.mobile-li-line-2 {
    width: 15px;
    height: 2px;
    background-color: white;
    transition: .5s all; }
.mobile-li-line-1-anim {
    transform: rotate(-45deg) translate(-5px, 5px);
    transition: .5s all; }
.mobile-li-line-2-anim {
    transform: rotate(45deg) translate(0px, 0px);
    transition: .5s all;
    width: 24px; }
.header-ul-mobile-list {
    display: inline-flex;
    list-style: none;
    flex-direction: column;
    background-color:rgba(40, 44, 51, 1) !important {}
    position: fixed;
    width: 100vw;
    height: 100vh;
    transform: translateY(-200vh);
    transition: .5s all ease-out;

    left: 0;
    margin: 0;
    row-gap: 30px;
    padding: 98px 0 0 16px;
    z-index: -99;
    div {
        position: absolute;
        top: 25px;
        left: 16px; }
    &_anim {
        z-index: 100;
        transform: translateY(0);
        transition: .5s all cubic-bezier(0.25, 0.1, 0.25, 1.0); }
    li {
        display: inline-flex;
        &::before {
            font-size: 30px; }
        a {
            font-size: 32px; } } }

.body-mobile-anim {
    position: fixed; }
